import { Languages } from '../data';

let last_id = 0;

export default {
	props: ['value'],
	
	data() {
		return {
			id: last_id++,
			languages: Languages
		}
	},
	
	computed: {
		langs() {
			return this.value ?? []
		}
	},
	
	methods: {
		clearSelection() {
			this.$refs.lang.selectedIndex = -1;
			this.onLangChange();
		},

		onLangChange() {
			const langs = [...this.$refs.lang.selectedOptions].map(opt => opt.value);
			
			this.$emit('input', langs);
		}
	}
	
}

