import {getLoader} from '../data';
const {get, has, deep_copy} = FrankerFaceZ.utilities.object;

const BAD_KEYS = [
	'id'
];

const BAD_COL_KEYS = [
	'id', 'cache'
];

let last_id = 0;

export default {
	props: ['tab', 'deletable'],

	data() {
		return {
			id: last_id++,
			edit_data: null,
			editing: false,
			copying: false,
			deleting: false,
			loader: 0
		}
	},

	computed: {
		json() {
			const copy = deep_copy(this.display);
			for(const key of BAD_KEYS)
				if ( has(copy, key) )
					delete copy[key];

			if ( ! Array.isArray(copy.columns) || copy.columns.length === 0 )
				copy.columns = undefined;
			else
				for(const column of copy.columns) {
					for(const key of BAD_COL_KEYS)
						if ( has(column, key) )
							delete column[key];
				}

			return JSON.stringify({
				type: 'tab',
				data: copy
			});
		},

		display() {
			if ( this.editing )
				return this.edit_data;

			return this.tab;
		},

		tags() {
			if ( ! this.display.tags )
				return null;

			return this.display.tags;
		},

		icon() {
			return this.display.icon;
		},

		title() {
			if ( this.display.i18n )
				return this.t(this.display.i18n, this.display.title);

			return this.display.title;
		}
	},

	created() {
		this.load = () => this.loader++;
	},

	methods: {
		edit() {
			this.edit_data = deep_copy(this.tab);
			if ( ! this.edit_data.tags )
				this.edit_data.tags = [];

			this.editing = true;
		},

		prepareCopy() {
			this.copying = true;
			requestAnimationFrame(() => {
				this.$refs.json.focus();
			});
		},

		save() {
			if ( this.edit_data ) {
				// TODO: Languages
				//const languages = getLoader().getLanguagesFromTags(this.edit_data.tags);
				//this.edit_data.lang = null; // languages.length ? languages : null;
			}

			this.$emit('save', this.edit_data);
			this.editing = false;
			this.edit_data = null;
		},

		cancel() {
			this.copying = false;
			this.editing = false;
			this.edit_data = null;
		}
	}
}

