const {generateUUID} = FrankerFaceZ.utilities.object;

export default {
	props: ['value', 'type', 'settings', 'inst'],

	data() {
		return {
			id: generateUUID()
		}
	},

	computed: {
		hasChannels() {
			return this.value?.settings?.ids?.length > 0;
		}
	}
}

