export default {
	props: ['value', 'type', 'settings', 'inst'],

	methods: {
		scroll(event) {
			if ( event.deltaY === 0 )
				return;

			const delta = event.deltaY > 0 ? 1 : -1;
			let new_val = this.value.settings.count + delta;

			if ( new_val < 0 )
				new_val = 0;
			else if ( new_val > 10 )
				new_val = 10;

			this.value.settings.count = new_val;
			event.preventDefault();
		}
	}
}

