const types = {
	live: ['LIVE', null],
	host: ['Hosting', 'stream-type-indicator__hosting-dot tw-border-radius-rounded'],
	rerun: ['Rerun', 'ffz-i-cw'],
	RECORDING: ['In-Progress Stream', 'ffz-i-camera'],
	ARCHIVE: ['Past Broadcast'],
	HIGHLIGHT: ['Highlight'],
	CLIP: ['Clip']
};

export default {
	props: {
		type: {
			type: String,
			required: true
		},
		icon: {
			type: String,
			required: false
		},
		label: {
			type: String,
			required: false
		}
	},

	computed: {
		useIcon() {
			if ( this.icon )
				return this.icon;

			const type = types[this.type];
			return type && type[1]
		},

		useLabel() {
			if ( this.label )
				return this.label;

			const type = types[this.type];
			return type && this.t('addon.deck.type.' + this.type, type[0]);
		}
	}
}

